import React, { Component } from 'react';
import { Trans, withTranslation } from 'react-i18next';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faStar } from '@fortawesome/free-solid-svg-icons';
import { Element as ScrollElement } from 'react-scroll';
import references from '../../../assets/json/references.json';
import '../../../assets/styles/references.css';

function Reference(props) {
  const { name, commentId, jobId, isActive } = props.props;
  return (
    <div className={`carousel-item reference-comment ps-5 ${isActive ? 'active' : ''}`} data-bs-interval='10000'>
      <p>
        <Trans>{commentId}</Trans>
      </p>
      <div className='d-flex justify-content-between align-items-center'>
        <div className='person'>
          <h1>{name}</h1>
          <Trans>{jobId}</Trans>
        </div>
        <div className='stars'>
          <FontAwesomeIcon icon={faStar} />
          <FontAwesomeIcon icon={faStar} />
          <FontAwesomeIcon icon={faStar} />
          <FontAwesomeIcon icon={faStar} />
          <FontAwesomeIcon icon={faStar} />
        </div>
      </div>
    </div>
  );
}

class References extends Component {
  render() {
    return (
      <section id='references' className='container py-5'>
        <ScrollElement name='references'>
          <div id='references-slider' className='mt-5 carousel carousel-dark slide d-flex justify-content-center align-items-center' data-bs-ride='carousel'>
            <ol className='carousel-indicators'>
              {Object.keys(references).map((_, i) => (
                <li key={i} data-bs-target='#references-slider' data-bs-slide-to={i} className={i === 0 ? 'active' : ''}></li>
              ))}
            </ol>
            <div className='carousel-inner'>
              {Object.keys(references).map((key, i) => {
                const person = references[key];
                return <Reference key={i} props={{ name: person.name, commentId: person.commentId, jobId: person.jobId, isActive: i === 0 }} />;
              })}
            </div>
            <a className='carousel-control-prev' href='#references-slider' role='button' data-bs-slide='prev'>
              <span className='carousel-control-prev-icon' aria-hidden='true'></span>
              <span className='visually-hidden'>Previous</span>
            </a>
            <a className='carousel-control-next' href='#references-slider' role='button' data-bs-slide='next'>
              <span className='carousel-control-next-icon' aria-hidden='true'></span>
              <span className='visually-hidden'>Next</span>
            </a>
          </div>
        </ScrollElement>
      </section>
    );
  }
}
export default withTranslation()(References);
